@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Rubik:wght@300;700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  min-height: 100%;
  /* make sure it is at least as tall as the viewport */
  position: relative;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #e8f1f9;
  height: 100%;
}

#App {
  overflow-x: hidden;
}

.dot {
  height: 25px;
  width: 25px;
  border-radius: 100%;
  position: absolute;
  opacity: 0;
  transform-origin: 0% 0%;
}

#hold {
  position: absolute;
  -webkit-animation: spin 5s linear infinite;
          animation: spin 5s linear infinite;
  top: 50%;
  left: 50%;
  z-index: 99;
}

#preloader {
  position: absolute;
  top: 0;
  left: 0;
  background-color: #000;
  width: 100%;
  height: 100%;
  z-index: 99;
}

@-webkit-keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes osc {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }
  50% {
    transform: scale(1.5);
    opacity: 1;
  }
  100% {
    transform: scale(0.5);
    opacity: 0;
  }
}

@keyframes osc {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }
  50% {
    transform: scale(1.5);
    opacity: 1;
  }
  100% {
    transform: scale(0.5);
    opacity: 0;
  }
}

@-webkit-keyframes rainbow {
  0% {
    background: #df2020;
  }
  25% {
    background: #80df20;
  }
  50% {
    background: #20dfdf;
  }
  75% {
    background: #8020df;
  }
  100% {
    background: #df2020;
  }
}

@keyframes rainbow {
  0% {
    background: #df2020;
  }
  25% {
    background: #80df20;
  }
  50% {
    background: #20dfdf;
  }
  75% {
    background: #8020df;
  }
  100% {
    background: #df2020;
  }
}

.nav-item {
  font-family: "Rubik", sans-serif;
  color: #0c0c14;
}

.mr-3 {
  font-size: 24px;
}

.nav-item:hover {
  color: #ff2d55;
