.navbar-toggler {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 2%;
  left: 2%;
  background: #e8f1f9;
  border-radius: 5px;
  font-size: 22px;
  box-shadow: -3px -3px 7px #ffffff, 3px 3px 5px #ceced1;
  border: none;
  color: #181818;
  z-index: 11;
}

.navbar-toggler > .navbar-toggler-icon {
  margin-left: -3px;
}

.navbar-toggler:hover {
  box-shadow: inset -3px -3px 7px #ffffff73, inset 3px 3px 5px rgba(18, 18, 19, 0.288);
}

.navbar-toggler:focus {
  border-color: transparent !important;
  outline: none;
}

#home-wrapper,
.wrapper {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
}

#home-wrapper .container-fluid,
.wrapper .container-fluid {
  margin: 0;
  padding: 0;
  height: 100%;
}

.hr_line {
  width: 60px;
  height: 10px;
  border-radius: 5px;
  box-shadow: inset -3px -3px 7px #ffffff73, inset 3px 3px 5px rgba(18, 18, 19, 0.288);
}

.hr_inner {
  width: 30px;
  height: 5px;
  border-radius: 5px;
  margin: 2.5px 15px;
  background: linear-gradient(120deg, #f56c6d 0, #f56c6d 100%);
}

.social-icons {
  margin: 15px 0 25px 0;
}

.social-icons a {
  background: #e8f1f9;
  box-shadow: -3px -3px 7px #ffffff, 3px 3px 5px #ceced1;
  position: relative;
  height: 40px;
  width: 40px;
  margin: 10px 10px;
  display: inline-flex;
  text-decoration: none;
  border-radius: 50%;
}

.social-icons a i {
  position: relative;
  z-index: 3;
  text-align: center;
  width: 100%;
  height: 100%;
  line-height: 40px;
}

.social-icons a:hover::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  border-radius: 50%;
  background: #e8f1f9;
  box-shadow: inset -3px -3px 7px #ffffff, inset 3px 3px 5px #ceced1;
}

.social-icons a.fb i {
  color: #4267b2;
}

.social-icons a.github i {
  color: #211f1f;
}

.social-icons a.linkedin i {
  color: #0e76a8;
}

.social-icons a.yt i {
  color: #ff0000;
}

#name {
  font-weight: 900;
  font-family: "Rubik", sans-serif;
}

mark {
  color: inherit;
  padding: 0 6;
  background: 0 0;
  background-repeat: repeat;
  background-size: auto;
  background-image: linear-gradient(120deg, #f56c6d 0, #f56c6d 100%);
  background-repeat: no-repeat;
  background-size: 100% 0.28em;
  background-position: 0 70%;
  position: relative;
}

.section-title {
  font-weight: 700;
  font-size: 3rem;
  color: rgba(244, 255, 255, 0.7);
  text-shadow: -3px -3px 6px rgba(255, 255, 255, 0.4), 3px 3px 6px rgba(0, 0, 0, 0.08);
  text-align: center;
  margin-top: 3rem;
}

#banner {
  display: flex;
  flex-direction: row;
  box-shadow: -7px -7px 20px 0px #fff9, -4px -4px 5px 0px #fff9, 7px 7px 20px 0px #0002, 4px 4px 5px 0px #0001, inset 0px 0px 0px 0px #fff9, inset 0px 0px 0px 0px #0001, inset 0px 0px 0px 0px #fff9, inset 0px 0px 0px 0px #0001;
  padding: 0;
  border-radius: 20px;
  overflow: hidden;
  flex: 0 0 30% !important;
}

#banner img {
  width: 14vw;
  height: 100%;
  object-fit: cover !important;
}

#banner .overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 14vw;
  background: linear-gradient(rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0));
}

#banner p {
  font-family: "Rubik", sans-serif;
  font-size: 1em;
}

#banner p h3 {
  color: rgba(0, 0, 0, 0.5);
  text-shadow: -3px -3px 7px #ffffff, 3px 3px 5px #ceced1;
  font-weight: 600;
  -webkit-animation: flyin 3s ease-out;
          animation: flyin 3s ease-out;
}

@-webkit-keyframes flyin {
  0% {
    transform: translateX(150%);
  }
  100% {
    transform: translateX(0%);
  }
}

@keyframes flyin {
  0% {
    transform: translateX(150%);
  }
  100% {
    transform: translateX(0%);
  }
}

