  width: 100%;
}

.timeline .timeline-container:last-of-type {
  margin-bottom: 0;
}

.timeline .content {
  position: relative;
  padding: 0;
}

.timeline .content .time {
  color: #8b88b1;
  font-size: 1em;
}

.timeline .content h3 {
  font-size: 20px;
  margin: 10px 0;
  text-shadow: -4px -4px 5px #fff9, 4px 4px 5px #0002;
}

.timeline .content p {
  margin: 0;
