}

.timeline .fas {
  margin-left: -2.3rem;
  color: #ff2d55;
  z-index: 1;
  margin-right: 1rem;
  position: absolute;
  background: #e8f1f9;
}

.timeline .fas.fa-briefcase {
  margin-left: -2.1rem;
}

.timeline span.line {
  position: absolute;
  width: 1px;
  background-color: #ff2d55;
  top: 3rem;
  bottom: 3rem;
  left: 1.5rem;
}

@-webkit-keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

.fadeInUp {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
  -webkit-animation: fadeInUp 3s;
          animation: fadeInUp 3s;
}

@media (max-width: 450px) {
