  }
}

.portfolio-item {
  box-shadow: -3px -3px 7px #ffffff, 3px 3px 5px #ceced1;
  margin-top: 15px;
  margin-bottom: 15px;
}

.portfolio-item img {
  height: 300px;
  width: 100%;
  min-width: 250px;
  padding: 15%;
  object-fit: contain;
}

@media only screen and (max-width: 676px) {
  .portfolio-filter {
    display: none;
  }
  .pf-filter-wrapper {
    display: block !important;
  }
  #invisible {
    display: none;
  }
}

.portfolio-item {
  position: relative;
  overflow: hidden;
}

.portfolio-item .thumb {
  overflow: hidden;
}

.portfolio-item .thumb img {
  -webkit-filter: opacity(0.5);
          filter: opacity(0.5);
}

.portfolio-item .details {
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 1;
}

.portfolio-item .details h4 {
  color: #fff;
  font-size: 20px;
  margin: 20px 0 10px 30px;
  padding: 10px;
  opacity: 0;
  transform: translateY(30px);
  transition: all cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
  position: absolute;
  top: 60px;
  text-shadow: 1px 1px 4px #0002, -1px -1px 4px #fff;
}

.portfolio-item .details span.term {
  color: #fff;
  background: #ff4c60;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  font-size: 14px;
  opacity: 0;
  display: inline-block;
  padding: 3px 10px;
  position: absolute;
  top: 0;
  left: 20px;
  transform: translateY(-40px);
  transition: all cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
  box-shadow: 3px 3px 6px #0002, -3px -3px 6px #fff;
}

.portfolio-item .details .more-button {
  color: #fff;
  font-size: 12px;
  display: block;
  height: 30px;
  position: absolute;
  bottom: 20px;
  right: 20px;
  opacity: 0;
  border: none;
  border-radius: 16px;
  transition: all 0.3s ease-in-out;
}

.portfolio-item .details .more-button::before, .portfolio-item .details .more-button::after {
  z-index: -1;
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  box-shadow: 3px 3px 6px #adcdd9, -3px -3px 6px #e9ffff;
  transition: opacity 0.1s ease;
  border-radius: 16px;
}

.portfolio-item .details .more-button::before {
  opacity: 1;
  background: linear-gradient(145deg, #b7d9e6, #d9ffff);
}

.portfolio-item .details .more-button::after {
  opacity: 0;
  background: linear-gradient(145deg, #d9ffff, #b7d9e6);
}

.portfolio-item .details .more-button:focus {
  outline: none !important;
  border: none;
}

.btn.btn-default {
  font-size: 16px;
  height: 50px;
  bottom: 20px;
  color: #06b5f5;
  border: none;
  border-radius: 16px;
  box-shadow: 3px 3px 6px #adcdd9, -3px -3px 6px #e9ffff;
  margin-left: 1.2rem;
}

.btn.btn-default:hover {
  color: #fff;
}

.btn.btn-default:focus {
  box-shadow: 3px 3px 6px #e9ffff, -3px -3px 6px #adcdd9;
}

.portfolio-item .mask {
  background: #e8f1f9;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0.05;
  height: 100%;
  width: 100%;
  transition: all 0.3s ease-in-out;
}

.portfolio-item:hover .mask {
  opacity: 1;
}

.portfolio-item:hover .details h4,
.portfolio-item:hover .details span {
  opacity: 1;
