@import url("https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap");
:root {
  --sidebar-flex: 1.5;
  --condiv-flex: 6;
  --fontsize-p: 1.2rem;
  --fontsize-nav: 1.2rem;
  --typing-font: 1.8rem;
  --background-color: #edf2f0;
}

body {
  font-family: "Raleway";
  background: var(--background-color);
}
.neu1 {
  width: 180px;
  height: 180px;
  margin: 10px;
  border-radius: 50%;
  box-shadow: -3px -3px 7px #ffffff73, 3px 3px 5px rgba(18, 18, 19, 0.288);
}
.neu2 {
  position: absolute;
  top: 2%;
  left: 12%;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  box-shadow: inset -3px -3px 7px #ffffff73,
    inset 3px 3px 5px rgba(18, 18, 19, 0.288);
}

.home {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.profilepic {
  border-radius: 50%;
  width: 160px;
  margin: 10px;
}
.typingeffect {
  font-size: var(--typing-font);
  margin-bottom: 30px;
  color: #6c757d;
}

a {
  text-decoration: none;
}

.subtopic {
  margin: 10px 10px 10px 15px;
  font-size: 1.6rem;
}
p {
  font-size: var(--fontsize-p);
  color: #6c757d;
  margin: 10px;
}
/* h1 {
  margin: 10px;
  font-size: 3.3rem;
} */
/* h2 {
  margin-top: 90%;
  font-size: 1.8rem;
} */
.abouthead {
  margin: 15px 10px;
  font-size: 2.3rem;
}
/* h3 {
  font-size: 1.4rem;
  margin: 37px 0 0 90px;
} */
h4 {
  font-size: 1.2rem;
  margin: 24px 0 0 90px;
}
.p1 {
  margin-left: 90px;
  font-size: 1rem;
}
.sk_head {
  position: absolute;
  top: 50%;
  left: 45%;
}
.sk {
  position: absolute;
  top: 65%;
  left: 58%;
  display: inline-block;
  width: 90px;
  height: 15px;
  font-weight: bolder;
  color: rgba(245, 108, 109, 1);
  text-align: center;
  padding: 10px;
  border-radius: 50px;
  box-shadow: inset -3px -3px 7px #ffffff73,
    inset 3px 3px 5px rgba(18, 18, 19, 0.288);
}
.s2 {
  left: 75%;
}
.s3 {
  top: 75%;
  left: 58%;
}
.s4 {
  top: 75%;
  left: 75%;
}
.s5 {
  top: 85%;
  left: 58%;
}
.s6 {
  top: 85%;
  left: 75%;
}

.line2 {
  margin: 1px 25px;
  height: 5px;
}
.inner2 {
  background: rgba(245, 108, 109, 1);
  width: 30px;
  height: 5px;
  border-radius: 5px;
  margin: 7px;
}
.vr_line {
  width: 10px;
  height: 210px;
  border-radius: 5px;
  position: absolute;
  top: 58%;
  left: 6.25%;
  box-shadow: inset -3px -3px 7px #ffffff73,
    inset 3px 3px 5px rgba(18, 18, 19, 0.288);
}
.vr_inner {
  width: 10px;
  height: 15px;
  border-radius: 5px;
  background: linear-gradient(
    120deg,
    rgba(245, 108, 109, 1) 0,
    rgba(245, 108, 109, 1) 100%
  );
}
.inn1 {
  margin-top: 45px;
}
.inn2 {
  margin-top: 80px;
}

.btnsame {
  background: #dde1e7;
  height: 50px;
  width: 50px;
  margin: 15px 20px;
  border-radius: 5px;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  transition: all 400ms;
  box-shadow: -3px -3px 7px #ffffff73, 3px 3px 5px rgba(18, 18, 19, 0.288);
  border: 1px solid rgba(255, 255, 255, 0.3);
  position: relative;
}
.btnsame i {
  font-size: 28px;
  transition: all 400ms;
  position: absolute;
  top: 20%;
  left: 25%;
  color: #6c757d;
}
.btn1:hover i {
  color: #43de85;
}
.btn2:hover i {
  color: #0088ee;
}
.btn3:hover i {
  color: #3b5998;
}
.btn4:hover i {
  color: #ee6644;
}
