.port_out {
  position: absolute;
  width: 38%;
  height: 30%;
  border-radius: 10px;
  box-shadow: inset -3px -3px 7px #ffffff73,
    inset 3px 3px 5px rgba(18, 18, 19, 0.288);
}
.port1 {
  top: 18%;
  left: 10%;
}
.port2 {
  top: 18%;
  left: 53%;
}
.port3 {
  top: 58%;
  left: 10%;
}
.port4 {
  top: 58%;
  left: 53%;
}
.port_in {
  position: absolute;
  width: 93%;
  height: 89%;
  margin: 3% 3%;
  border-radius: 10px;
  box-shadow: -3px -3px 7px #ffffff73, 3px 3px 5px rgba(18, 18, 19, 0.288);
}
.po1 {
  width: 96%;
  height: 93%;
  margin: 2% 2%;
  border-radius: 10px;
}
.line3 {
  position: absolute;
  top: 70%;
  left: 45%;
}
.inner3 {
  margin: 3px 15px;
  background: rgba(245, 108, 109, 1);
  width: 30px;
  height: 5px;
  border-radius: 5px;
}
.con_out {
  border-radius: 10px;
  box-shadow: inset -3px -3px 7px #ffffff73,
    inset 3px 3px 5px rgba(18, 18, 19, 0.288);
}
.con_in {
  padding: 0;
  margin: 15px 15px;
  border-radius: 10px;
  box-shadow: -3px -3px 7px #ffffff73, 3px 3px 5px rgba(18, 18, 19, 0.288);
}
.co1 {
  width: 100%;
  height: 100%;
  border-radius: 10px;
}

.mode {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 2%;
  right: 2%;
  background: #e8f1f9;
  border-radius: 25px;
  font-size: 22px;
  cursor: pointer;
  box-shadow: -3px -3px 7px #ffffff, 3px 3px 5px rgba(18, 18, 19, 0.288);
}
.mode:hover {
  box-shadow: inset -3px -3px 7px #ffffff, inset 3px 3px 5px #ceced1;
}
.i1 {
  position: absolute;
  top: 13px;
  left: 15px;
}
.i2 {
  color: #6c757d;
}
.i3 {
  color: rgba(245, 108, 109, 1);
}
.back2 {
  top: 55%;
  left: 4%;
  z-index: 1;
}
.back3 {
  width: 210px;
  height: 25px;
  padding: 12px 0;
  position: absolute;
  top: 54%;
  left: 22%;
  background: rgba(245, 108, 109, 1);
  border-radius: 25px;
  font-size: 22px;
  text-align: center;
  cursor: pointer;
  box-shadow: -3px -3px 7px #ffffff73, 3px 3px 5px rgba(18, 18, 19, 0.288);
}
.back3:hover {
  box-shadow: inset -3px -3px 7px #ffffff73,
    inset 3px 3px 5px rgba(18, 18, 19, 0.288);
}
.back4 {
  left: 56%;
}

@media all and (max-width: 450px) {
  :root {
    --fontsize-nav: 1.2rem;
    --sidebar-flex: 0;
    --typing-font: 1.4rem;
  }
  body {
    height: 1400px;
  }

  .sidebar {
    padding: 30px;
  }
  /* .mode {
    right: 5%;
  } */
  nav {
    width: 90%;
    top: 15%;
    left: 5%;
  }
  nav ul li {
    margin: 10px;
  }
  .neu2 {
    top: 5%;
    left: 19%;
  }
  h2 {
    margin-top: 80%;
    font-size: 2.2rem;
  }
  p {
    font-size: 1.4rem;
  }
  h1 {
    text-align: center;
    font-size: 2.8rem;
  }
  .p10 {
    display: none;
  }
  .back2 {
    top: 27%;
  }
  .vr_line {
    top: 30.5%;
    left: 10.5%;
  }
  /* h3 {
    margin-top: 25px;
  } */
  .sk_head {
    left: 0;
    top: 65%;
    margin-left: 30px;
  }
  .sk {
    top: 74%;
    left: 10%;
  }
  .s2 {
    left: 50%;
  }
  .s3 {
    top: 82%;
  }
  .s4 {
    top: 82%;
    left: 50%;
  }
  .s5 {
    top: 90%;
  }
  .s6 {
    top: 90%;
    left: 50%;
  }
  .subtopic {
    margin: 10px !important;
  }
  /* .con_out {
    width: 88%;
