  } */
  /* .con_in {
    width: 94%;
    height: 86%;
  } */
  .back3 {
    top: 44%;
    left: 19%;
  }
  .back4 {
    top: 54%;
    left: 19%;
  }
  .line2 {
    margin: 1px 120px;
  }
  .inner2 {
    margin: 15px;
  }
  .port_out {
    width: 80%;
    height: 26%;
  }
  .port1 {
    top: 12%;
  }
  .port2 {
    left: 10%;
    top: 42%;
  }
  .port3 {
    display: none;
  }
  .port4 {
    top: 72%;
